.app-wrapper {
    display: grid;
    width: 900px;
    margin: 0 auto;
    grid-template-areas:
        "h h"
        "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 1px;

    }
.app-wrapper-content{
    grid-area: c;
    background-color: #3e82ff;
    }


